.app {
  background-color: black;
}

.banner {
    position: relative;
    color: white;
    object-fit: contain;
    height: 480px;
}
.banner-parts {
    margin-left: 30px;
    padding-top: 150px;
    
}
.banner-title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner-description {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    overflow: hidden; 
    font-size: 1.1rem;
    max-width: 300px;
    height: 90px;
    padding-bottom: 0.55rem;

}
.banner-button {
    outline:#111;
    border-color: black;
    cursor: pointer;
    font-size: medium;
    color: black;
    margin-right: 1rem;
background-color: grey;
padding-left: 2rem;
padding-right: 2rem;
padding-top: 0.5rem;
padding-bottom: 0.5rem;
}
.banner-button:hover {
    color: black;
    background-color: bisque;
}
.banner-fadeBottom {
    /*to add a fade effect in the bottom of the banner */
height: 8rem;
background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
);
}
.nav {

    max-height: 80px;
    width: 100%;
    top: 0;
    position: fixed; 
    z-index: 1;  
/*    z-index defines the order of overlapping HTML elements. Elements with a higher index will be placed on top of elements with a lower index.
 */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
 .nav-color {
    background-color: black;
 }
.nav-parts {
   
   height: 80px;
   display: flex;
   justify-content: space-between;
}
.nav-logo {
    width: 100px;
    height: 70px;
    position: fixed;
left: 20px;
cursor: pointer;
}
.nav-avatar {
    width: 70px;
    height: 70px;
    position: fixed;
    right: 20px;
    cursor: pointer;

}

.row {
    margin-left: 10px;
  max-height: 200px;
}
.row-title{
    color: antiquewhite;
    padding: 10px; 

}

.row-collection {
display: flex;
overflow-y: hidden;
overflow-x: scroll;
}
.row-collection::-webkit-scrollbar {
    display: none;
}
.row-image {
    max-height: 80px;
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
}
.row-image:hover {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
    opacity: 1;
}
.row-largeimage {
max-height: 150px;
}
.row-largeimage:hover {
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    opacity: 1;
}

.signin {
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: black;
}
.signup {
    color: gray;
}
.signup-link:hover {
    cursor: pointer;
    text-decoration: underline;
}
.signin > form > h1 {
    text-align: left;
    margin-bottom: 25px;
}
.signin > form {
 display: grid;
 flex-direction: column;
}
.signin > form > input {
 outline-width: 0;
 height: 40px;
 margin-bottom: 14px;
}
.signin > form > button {
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
}
.login-page {
    position: relative;
    height: 100%;
    background: url('https://t4.ftcdn.net/jpg/03/71/56/17/360_F_371561715_LVI4qVJ2hyWMDXdqJNGdktggEzjQuC15.jpg') center no-repeat;
    background-size: cover;
}

.loginPage-logo {
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
}

.signIn-button {
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size: 1rem;
    color:bisque;
    background-color: #e50914;
}
.loginPage-gradient {
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.8) 0,
    rgba(0, 0, 0, 0) 60%,
    rgba(0, 0, 0, 0.8) 100%
    );
}
.login-component > h1 {
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.login-component > h2 {
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}
.login-component > h3 {
    font-size: 1.3rem;
    font-weight: 400;
}
.login-component {
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    z-index: 1;
color: white;
padding: 20px;
left:0;
right: 0;
}

.signUp-form {
    margin: 20px;
}
.signUp-form > form > input {
 padding: 10px;
 outline-width: 0;
 height: 30px;
 width: 30%;
 border: none;
 max-width: 600px;
}

.signUp-form > form > button {
    padding: 16px 20px;
    font-size: 1rem;
    color:aliceblue;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}
.payment-data{
    background-color: rgb(188, 186, 186);
    margin-top: 5px;
    padding: 10px;
    font-size: 12px;
    padding-left: 10px;
    color: rgb(49, 49, 80);
}
.final-plan{
    padding: 0px 20px;
    font-size: 1rem;
    margin-top: 5%;
    color: rgb(202, 79, 79);
}
.button-plan{
    margin-left: 70%;
    background-color: rgb(241, 139, 139);
    border-color: rgb(242, 103, 103);
}
.profile-page {
    height: 130vh;
    color: white;
}
.profile-component {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding-top: 8%;
    max-width: 800px;
}
.profile-component > h1 {
font-size: 30px;
font-weight: 400;
border-bottom: 1px solid grey;
margin-bottom: 20px;
color: grey;
}
.profile-data {
    display: flex;
}
.profile-data > img {
    height: 100px;
}
.profile-details {
    color: white;
    margin-left: 25px;
    flex:1 1;;
}
.profile-details > h3 {
    background: linear-gradient(
        90deg,
        rgb(235, 110, 110) 0%,
        rgb(234, 199, 166) 100%
      );
    color: black;
    padding: 5px;
    font-size: 10px;
    padding-left: 20px;
    border-radius: 2px;
}
.user-plans > h2 {
    border-bottom: 1px solid darkblue;
    padding-bottom: 5px;
    color: rgb(49, 49, 80);
}
.profile-button{
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 3%;
    width: 100%;
    color: white;
    background-color: rgb(215, 77, 77);
    font-weight: 600;
    border: none;
    cursor: pointer;
}
.plan-detail {
    background-color: rgb(188, 186, 186);
    padding: 10px;
    font-size: 12px;
    padding-left: 10px;
    color: rgb(49, 49, 80);
    
}
.plan-detail > h4 {
    font-size: small;
    color: brown;
    -webkit-text-decoration-style:wavy;
            text-decoration-style:wavy;

}
.check-icon {
 margin-left: 300px;
 padding-left: 120px;
 
}

.payment-page {
    height: 130vh;
    color: rgb(16, 3, 3);
    background-color: rgb(251, 251, 246);
}
.payment-message{
    flex: 1 1;
    width: 50%;
    margin-left: 400px;
    padding-left: 20px;
    padding-top: 15%;
    max-width: 400px;
    font-size: small;
}
.netf-image {
   flex:1 1;
